<template>
  <div class="row" style="position: relative">
    <!-- <transition name="rightToLeft"> -->
    <div
      class="offset-xl-4 offset-lg-3 offset-md-2 offset-sm-2 col-xl-4 col-lg-6 col-md-8 col-sm-8 scrolling-wrapper p-0 border border-top-0 border-bottom-0"
      id="scrolling-wrapper"
    >
      <div class="card" style="width: 100%">
        <div class="card-header">
          <h4 class="text-center">Would you like to</h4>
        </div>
        <div class="card-body row mx-auto">
          <div class="col-6">
            <div class="card" style="width: 100%">
              <div class="card-header text-center">
                <h6 class="text-info">CREATE</h6>
              </div>
              <div class="card-body text-center">
                <p style="white-space: pre-wrap">Create a new group and invite friends.</p>
              </div>
              <div class="card-footer d-flex justify-content-center">
                <button
                  type="button"
                  class="btn btn-info btn-raised"
                  @click="chooseCreateGroup()"
                >Create a group</button>
              </div>
            </div>
          </div>
          <div class="col-6">
            <div class="card" style="width: 100%">
              <div class="card-header text-center">
                <h6 class="text-success">JOIN</h6>
              </div>
              <div class="card-body text-center">
                <p style="white-space: pre-wrap">Enter a invite code and join your friends.</p>
              </div>
              <div class="card-footer d-flex justify-content-center">
                <button
                  type="button"
                  class="btn btn-success btn-raised"
                  @click="chooseJoinGroup()"
                >Join a group</button>
              </div>
            </div>
          </div>
        </div>
        <div class="card-footer"></div>
      </div>
      <!-- </transition>
      <transition name="rightToLeft">-->
      <div class="card" style="width: 100%" v-if="isCreate">
        <div class="card-header text-center text-info">
          <h5>CREATE YOUR GROUP</h5>
        </div>
        <div class="card-body">
          <form>
            <div class="form-group">
              <span class="small" :class="{'text-danger': alert}">GROUP NAME</span>
              <i class="text-danger small">{{alert}}</i>
              <input
                type="text"
                class="form-control"
                id="groupName"
                placeholder="Enter a group name"
                v-model="groupName"
              >
              <p style="white-space:pre-wrap">After created a group, you can invite your friends via invite code.</p>
            </div>
          </form>
        </div>
        <div class="card-footer d-flex">
          <button class="btn btn-flat" @click="backToMain()">
            <i class="fas fa-arrow-left"></i> Back
          </button>
          <button class="btn btn-info ml-auto" @click="createGroup()">CREATE</button>
        </div>
      </div>
      <!-- </transition>
      <transition name="rightToLeft">-->
      <div class="card" style="width: 100%" v-if="isJoin">
        <div class="card-header text-center text-success">
          <h5>JOIN A GROUP</h5>
        </div>
        <div class="card-body">
          <form>
            <div class="form-group">
              <span class="small" :class="{'text-danger': alert}">INVITE CODE</span>
              <i class="text-danger small">{{alert}}</i>
              <input
                type="text"
                class="form-control"
                id="inviteCode"
                placeholder="Enter a invite code"
                v-model="inviteCode"
              >
              <p style="white-space:pre-wrap">Enter your friend invite code to join their group.</p>
            </div>
          </form>
        </div>
        <div class="card-footer d-flex">
          <button class="btn btn-flat" @click="backToMain()">
            <i class="fas fa-arrow-left"></i> Back
          </button>
          <button class="btn btn-success ml-auto" @click="joinGroup()">JOIN</button>
        </div>
      </div>
    </div>
    <!-- </transition> -->
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "GroupModal",
  data() {
    return {
      isCreate: true,
      isJoin: false,
      groupName: "",
      inviteCode: "",
      alert: ""
    };
  },
  computed: {
    ...mapState({
      unsub: state => state.postView.unsubPost,
      post: state => state.postView.post,
      userInfo: state => state.auth.userInfo,
      getUserStatus: state => state.auth.userInfo.status,
      getUsername: state => state.auth.userInfo.name,
      notify: state => (state.auth.notify ? state.auth.notify : []),
      inactiveNotify: state =>
        state.auth.inactiveNoti ? state.auth.inactiveNoti : [],
      chatList: state => state.chat.chatList,
      pendingRequests: state => state.auth.pendingRequests,
      friendNotify: state => state.auth.friendNotify,
      converList: state => state.chat.converList,
      groupId: state => state.group.newGroupId,
      groupCreateMessage: state => state.group.createGroupStatus
    })
  },
  watch: {
    groupCreateMessage(n) {
      if (n == "success" && this.groupId) {
        console.log(n, this.groupId);
        this.$router.push("/group/" + this.groupId);
      }
    }
  },
  methods: {
    chooseCreateGroup: function() {
      let scrolling = document.getElementById("scrolling-wrapper");
      this.isCreate = true;
      this.isJoin = false;
      scrolling.scrollLeft = scrolling.scrollWidth / 2;
    },
    chooseJoinGroup: function() {
      let scrolling = document.getElementById("scrolling-wrapper");
      this.isCreate = false;
      this.isJoin = true;
      scrolling.scrollLeft = scrolling.scrollWidth / 2;
    },
    backToMain: function() {
      let scrolling = document.getElementById("scrolling-wrapper");
      // this.isCreate = true;
      // this.isJoin = false;
      scrolling.scrollLeft = 0;
    },
    createGroup: function() {
      if (this.groupName == "") {
        this.alert = " - This field is required";
        return;
      }
      let group = {
        owner: this.userInfo.name,
        ownerId: this.userInfo._id,
        groupName: this.groupName
      };
      console.log(group);
      this.$store.dispatch("createGroup", group);
    },
    joinGroup: function() {
      if (this.inviteCode == "") {
        this.alert == " - Please enter invite code";
        return;
      }
      let data = {
        inviteCode: this.inviteCode, userId: this.userInfo._id
      };
      this.$store.dispatch('joinGroup', data);
    }
  }
};
</script>

<style scoped>
.scrolling-wrapper {
  overflow-x: hidden;
  overflow-y: hidden;
  white-space: nowrap;
  margin-top: 10vh;
  scroll-behavior: smooth;
}
.card {
  display: inline-block;
  margin-right: 3px;
}
.rightToLeft-enter-active,
.rightToLeft-leave-active {
  animation: slide2 0.5s forwards;
  /* animation-delay: 0.5s; */
  position: absolute;
}
.rightToLeft-enter, .rightToLeft-leave-to /* .fade-leave-active below version 2.1.8 */ {
  animation: slide 0.5s forwards;
  position: absolute;
  /* animation-delay: .2s; */
}
#slide {
  position: absolute;
  left: -600px;
  width: 600px;
  height: 370px;
  background: blue;
  -webkit-animation: slide 0.5s forwards;
  -webkit-animation-delay: 2s;
  animation: slide 0.5s forwards;
  animation-delay: 2s;
}

@keyframes slide {
  0% {
    left: 0;
    opacity: 0;
    width: 0;
  }
  25% {
    opacity: 0;
    left: -16%;
  }
  50% {
    left: -33%;
  }
  100% {
    left: -50%;
  }
}
@keyframes slide2 {
  0% {
    left: -50%;
    opacity: 0;
  }
  25% {
    left: -33%;
  }
  50% {
    left: -16%;
  }
  100% {
    left: 0;
    opacity: 1;
  }
}

.isCreate {
  position: absolute;
  height: 500px;
  width: 35vw;
  transform: translate(4%, -30%);
  z-index: 1;
}
.isJoin {
  position: absolute;
  height: 500px;
  width: 35vw;
  transform: translate(-53%, -30%);
  z-index: 1;
}
</style>



